
.prefooter {
	padding: 80px 0 30px;
	@include r(800) {
		padding: 10px 0; }
	.layout-s {
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap; }
	&__wrap {
		margin-bottom: 20px;
		&_wide {
			width: 50%;
			@include r(1024) {
				width: 100%; } }
		&_short {
			width: 40%;
			@include r(1024) {
				width: 100%; } } } }

