
.footer {
	padding: 24px 0;
	background-color: #ededed;
	@include r(600) {
		padding-bottom: 0; }
	p {
		font-size: 16px;
		color: $black;
		margin-bottom: 1em;
		@include mr; }
	&__wrap {
		flex: 0 1 auto;
		@include r(900) {
			margin-bottom: 20px; }
		&_wide {
			flex: 1 1 100%;
			@include r(450) {
				flex-wrap: wrap; } } }

	&__list {
		padding: 0;
		font-size: 0;
		list-style: none;
		li {
			display: inline-block;
			vertical-align: top;
			margin-left: 80px;
			margin-bottom: 10px;
			@include r(600) {
				margin-left: 0;
				display: block;
				margin-bottom: 16px; } }
		a {
			font-size: 14px;
			color: #9a9a9a;
			@include mm;
			&:hover {
				text-decoration: none; } } }
	.layout-s {
		display: flex;
		width: 100%;
		@include r(900) {
			display: block; } }
	.logo {
		white-space: nowrap;
		margin-bottom: 10px;
		svg {
			fill: $dark; } }
	.copy {
		margin-bottom: 0;
		font-size: 12px;
		color: #838383;
		margin-bottom: 0;
		@include ml; } }


.listik {
	padding: 0 0 0 10%;
	margin: 0;
	list-style: none;
	@include r(900) {
		padding-left: 0; }
	@include r(450) {
		width: 100%; }
	li {
		margin-bottom: 10px; }
	a {
		color: $black;
		@include mm;
		@include tr(.4s);
		&:hover {
			color: #9a9a9a; } } }

