

.breadcrumbs {
	padding: 0;
	margin: 0;
	list-style: none;
	text-align: center;
	li {
		display: inline-block;
		vertical-align: top;
		position: relative;
		font-size: 14px;
		color: $dark;
		@include mm; }
	a {
		position: relative;
		display: block;
		padding-right: 20px;
		color: $red;
		@include msb;
		@include tr(.4s);
		&:after {
			content: '';
			position: absolute;
			top: .8em;
			right: 5px;
			@include try(-50%);
			@include arr(6, 8, $red, r); }
		&:hover {
			color: $yellow; } } }

.main {
