	display: block;
	overflow: hidden;
	background-color: #f8f8f8;
	margin-bottom: 30px;
	z-index: 1;
	&__list {
		padding: 0;
		margin: 10px 0 0;
		list-style: none;
		li {
			display: inline-block;
			vertical-align: top;
