			// height: 26px
			margin: 10px 5px; }
		a {
			display: block;
			@include tr(.4s);
			&:hover {
				opacity: .6; } }
		svg {
			height: 26px; } }
	p {
		display: block;
		position: relative;
		padding: 10px;
		font-size: 16px;
		color: #fff;
		@include meb;
		@include r(900) {
