		align-items: center;
		justify-content: space-between;
		width: 100%;
		margin-top: 20px;
		@include r(900) {
			width: 100%;
			flex-wrap: wrap; }
		@include r(600) {
			justify-content: center;
			margin-top: 0; } }
	.btn-t {
		width: 48%;
		@include r(900) {
			margin: 20px 0 0; }
		@include r(600) {
			width: 100%; } }

	.filter {
		flex-grow: 1;
		margin-left: 20px;
		width: auto;
		height: 46px;
		@include r(600) {
			width: 100%;
			max-width: 100%;
			margin: 20px 0 0; }
		&__text {
			height: 46px;
			line-height: 46px; } } }

.filter {
	position: relative;
	display: inline-block;
	cursor: pointer;
	font-size: 0;
	white-space: nowrap;
	z-index: 10;
	width: 100%;
	border-radius: 40px;
	padding: 0;
	height: 40px;
	&:after {
		content: "";
		position: absolute;
		top: 50%;
		right: 18px;
		transform: translateY(-50%);
		transform-origin: 50% 50%; // 50% 20%
		@include arr(10, 5, $black, b);
		// +tr(.4s 0s)
		z-index: 1; }
	&:hover {
		box-shadow: 0 0 10px 0 rgba(0, 0, 0, .2); }

	&.is-active {
		z-index: 1000;
		box-shadow: 0 0 10px 0 rgba(0, 0, 0, .2);
		&:after {
			top: 45%;
			right: 10px;
			@include rotate(180); }
		.filter__text {
			border-color: transparent;
			border-radius: 20px 20px 0 0; } }
	&__text {
		position: relative;
		height: 40px;
		font-size: 16px;
		color: $black;
		padding: 0 30px 0 20px;
		vertical-align: middle;
		line-height: 40px;
		user-select: none;
		border: 1px solid #c8c8c8;
		background-color: #fff;
		border-radius: 20px;
		z-index: 1;
		@include mr;
		span {
			font-size: 16px;
			color: $black;
			@include mr; } }

	&__list {
		position: absolute;
		display: none;
		left: 0px;
		right: 0px;
		top: 20px;
		padding: 20px 0 0;
		list-style: none;
		border-radius: 0 0 20px 20px;
		background: #fff;
		overflow: hidden;
		box-shadow: 0 0 30px rgba(0, 0, 0, .1);
		&:before {
			content: '';
			@include coverdiv;
			z-index: -5;
			box-shadow: 0 0 10px 0 rgba(0, 0, 0, .2); } }

	&__link {
		position: relative;
		width: 100%;
		font-size: 14px;
		line-height: 36px;
		padding-left: 10px;
		color: #000;
		cursor: pointer;
		border-top: 1px solid $black;

		transition: background-color .8s, border .8s, color .4s;
		&:hover {
			background-color: $red;
			border-top: 1px solid $red;
			color: #fff; } }

	&__row {
		position: relative;
		width: 100%;
		font-size: 14px;
		line-height: 36px;
		padding-left: 20px;
		color: #000;
		cursor: pointer;
		@include tr(.4s);
		&:hover {
			background-color: rgba(204, 204, 204, .5); }
		.btn {
			margin-top: 20px; } }
	&__all {
		display: inline-block;
		color: $red;
		line-height: .8;
		border-bottom: 1px solid $red;
		&:hover {
			border-color: $n; } } }

/* Rating */

.rating {
	display: inline-block;
	vertical-align: top;
	margin: 0 0 5px 20px;
	padding: 0;
	border: none;
	&__caption {
		margin-bottom: 0.5em;
		padding: 0; }
	&__group {
		position: relative;
		width: 5em;
		height: 1em;
		background-image: url('../img/content/star-gray.svg');
		background-size: 1em 1em;
		background-repeat: repeat-x; }
	&__input {
		position: absolute;
		width: 1px;
		height: 1px;
		overflow: hidden;
		clip: rect(0 0 0 0); }
	// &__input:focus ~ .rating__focus
	// 	position: absolute
	// 	top: -0.1em
	// 	right: -0.1em
	// 	bottom: -0.1em
	// 	left: -0.1em
	// 	z-index: -1
	// 	outline: 0.1em solid #4a90e2
	&__star {
		position: absolute;
		top: 0;
		left: 0;
		margin: 0;
		height: 1em;
		background-size: 1em 1em;
		background-repeat: repeat-x;
		cursor: pointer;
		&:nth-of-type(1) {
			z-index: 5;
			width: 1em; }
		&:nth-of-type(2) {
			z-index: 4;
			width: 2em; }
		&:nth-of-type(3) {
			z-index: 3;
			width: 3em; }
		&:nth-of-type(4) {
			z-index: 2;
			width: 4em; }
		&:nth-of-type(5) {
			z-index: 1;
			width: 5em; } }
	&__info {
		display: inline-block;
		vertical-align: top;
		margin-left: 10px;
		color: #bcbcbc; } }
.rating__input:hover,
.rating__input:checked + .rating__star {
			background-image: url('../img/content/star.svg'); }
.rating__input:hover ~ .rating__star {
			background-image: url('../img/content/star-gray.svg'); }

.check {
	position: relative;
	display: inline-block;
	input {
		position: absolute;
		top: 2px;
		left: 0;
		z-index: 2;
		opacity: 0;
		&:checked {
			~ span {
				user-select: none;
				&:before {
					background-color: #fff; }
				&:after {
					content: "";
					position: absolute;
					top: 4px;
					left: 4px;
					display: inline-block;
					vertical-align: top;
					width: 10px;
					height: 8px;
					background-image: url('../img/content/icon-selector-checked.png');
					// @include bg(icon-selector-checked)
 } } }					// +s(icon-selector-checked)
		&:disabled {
			~ span {
				opacity: 1; } } }
	span {
		position: relative;
		padding-left: 28px;
		left: 0;
		top: 0;
		font-size: 14px;
		color: #fff;
		cursor: pointer;
		user-select: none;
		@include mr;
		z-index: 15;
		@include r(380) {
			display: inline-block;
			max-width: 88vw; }
		&:before {
			content: "";
			position: absolute;
			top: -1px;
			left: 0;
			width: 18px;
			height: 18px;
			background-color: #fff;
			border-radius: 100%;
			border: 1px solid $yellow;
			cursor: pointer;
			@include tr(.4s); } } }

