.list {
	&-num {
		padding: 0;
		margin: 0;
		list-style: none;
		li {
			padding: 0;
			margin: 0; }
		b {
			font-size: 110%;
			@include mb; } }
	&-arrow {
		padding: 0;
		margin: 0 0 20px;
		list-style: none;
		li {
			position: relative;
			padding-left: 10px;
			margin-bottom: 4px;
			&:before {
				content: '\003E';
				position: absolute;
				top: .4em;
				left: 0;
				color: $red;
				font-size: 10px;
				@include meb; } }
		&.mod-black {
			li {
				padding: 0;
				text-indent: 14px;
				&:before {
					color: $black;
					left: -14px; } } } }
	&-dots {
		padding: 0 0 0 20px;
		margin: 0 0 20px;
		list-style: disc; }
	&-underline {
		padding: 0;
		margin: 0 0 40px;
		list-style: none;
		li {
			padding: 5px;
			border-bottom: 1px solid #e5e5e5; }
		a {
			color: $red;
			@include mm;
			&:hover {
				text-decoration: underline; } } }
	&-decimal {
		padding: 0;
		margin: 0 0 20px;
		list-style: none;
		text-indent: 14px;
		> li {
			position: relative;
			counter-increment: step-counter;
			&:before {
				content: counter(step-counter)'.';
				position: absolute;
				top: .0em;
				left: -14px;
				margin-right: 5px; } }
		.list-arrow li,
		.list-arrow p {
			margin-bottom: 0px; } }
	&-line {
		padding: 0;
		margin: 20px 0;
		list-style: none;
		li {
			position: relative;
			padding-left: 20px;
			&:before {
				content: '';
				position: absolute;
				top: .8em;
				left: 0;
				height: 1px;
				width: 14px;
				background-color: $yellow; } } }
	&-target {
		padding: 0;
		margin: 20px 0;
		list-style: none;
		li {
			position: relative;
			padding-left: 50px;
			height: auto;
			min-height: 40px;
			margin-bottom: 16px;
			&:before {
				content: '';
				position: absolute;
				top: 0;
				left: 0;
				width: 36px;
				height: 36px;
				background-image: url('../img/content/target.png'); } } } }

