
.readl {
	padding: 40px 0 90px;
	&__slider {
		margin-left: -10px;
		@include r(850) {
			margin-left: -5px; } }
	&__item {
		display: flex;
		height: auto;
		margin-left: 20px;
		color: $black;
		@include tr(.4s);
		&:hover {
			opacity: .8; }
		h3 {
			font-size: 20px;
			margin-bottom: 10px;
			@include msb; } }
	&__pic {
		width: 100%;
		margin-bottom: 14px;
		img {
			width: 100%; } } }
