
.eventWide {
	position: relative;
	background-color: $yellow;
	border-radius: 6px;
	margin-bottom: 30px;
	overflow: hidden;
	&__header {
		display: block;
		overflow: hidden;
		span {
			display: inline-block;
			vertical-align: top;
			font-size: 16px;
			border-radius: 6px;
			padding: 15px 10px 10px 15px;
			color: $yellow;
			background-color: #0c0c0c;
			@include ml; }
		b {
			@include msb; } }
	&__content {
		padding: 20px;
		h3 {
			font-size: 24px;
			margin-bottom: 14px;
			color: $black;
			@include mb; }
		h4 {
			font-size: 14px;
			text-transform: uppercase;
			@include mb; } }
	&__box {
		margin-bottom: 26px;
		p {
			@include ml; } }
	&__footer {
		display: flex;
		align-items: center;
		justify-content: space-between;
		flex-wrap: wrap; }
	&__pic {
		border-radius: 4px;
		overflow: hidden; }
	&-video {
		position: relative;
		&:before {
			content: '';
			@include coverdiv;
			background-color: rgba(0, 0, 0, .4); }
		&:after {
			content: '';
			@include vcenter;
			@include arr(10, 16, #fff, r); } }
	.arrow-list {
		li {
			margin-bottom: 0; } }
	.btn {
		padding: 20px 30px;
		max-width: 230px;
		@include r(650) {
			max-width: 100%;
			width: 100%; } }
	.arrow {
		position: absolute;
		top: 50%;
		@include try(-50%);
		&-left {
			left: -40px; }
		&-right {
			right: -40px;
			left: auto; }
		svg {
			fill: $red;
			height: 28px; } }

	.carusel {
		position: relative;
		width: 50%;
		padding: 0 40px;
		@include r(650) {
			width: 100%;
			padding: 0 20px;
			margin-top: 30px; } } }


.eventWide {
	.slick-list {
		margin-left: 10px; }
	.slick-track {
		margin-left: -10px; }
	&__item {
		font-size: 0;
		margin-left: 10px; } }

