.feedback {
	padding: 80px 0 50px;
	@include r(650) {
		padding-top: 50px; }
	&__item {
		display: flex!important;
		justify-content: space-between;
		flex-wrap: wrap; }
	&__pic {
		width: 48%;
		@include r(800) {
			width: auto;
			max-width: 96%;
			margin: 0 auto;
			text-align: center; } }
	&__img {
		@include r(800) {
			display: inline-block; } }
	&__content {
		width: 50%;
		@include r(800) {
			width: 100%;
			margin-top: 20px; } }
	&__header {
		margin-bottom: 40px;
		h3 {
			font-size: 28px;
			color: $black;
			@include meb; }
		.subtitle {
			text-transform: uppercase; }
		p {
			font-size: 16px;
			font-size: 16px;
			color: #696969;
			@include mr; } }
	&__do {
		h3 {
			font-size: 16px;
			text-transform: uppercase;
			color: $black;
			@include mb; } }
	&__result {
		h3 {
			font-size: 16px;
			text-transform: uppercase;
			color: $yellow;
			@include mb; } }
	.btn {
		box-shadow: none; }
	.carusel {
		margin: 50px 0 0;
		@include r(1250) {
			padding: 0 40px; } }
	.arrow {
		position: absolute;
		top: 50%;
		@include try(-50%);
		&-left {
			left: -40px; }
		&-right {
			right: -40px;
			left: auto; }
		svg {
			fill: $red;
			height: 28px; } } }
