
.cifra {
	padding: 50px 0 10px;
	background: $red url('../img/content/bg-red.jpg');
	.title__section {
		color: #fff;
		@include r(650) {
			font-size: 30px; } }
	&__list {
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;
		width: 100%;
		max-width: 1000px;
		margin: 0 auto 20px;
		padding: 0;
		list-style: none;
		li {
			display: inline-block;
			vertical-align: top;
			width: 46%;
			margin-bottom: 40px;
			color: #fff;
			@include r(850) {
				width: 100%; }
			@include r(650) {
				margin-bottom: 20px; } } }
	.desc__line {
		font-size: 36px;
		@include mbl;
		@include r(650) {
			font-size: 26px; } }
	p {
		text-transform: uppercase; }
	.footnote {
		p {
			font-size: 14px;
			color: #fff;
			text-transform: none; } } }
