.filter {
  position: relative;
  display: inline-block;
  cursor: pointer;
  font-size: 0;
  white-space: nowrap;
  z-index: 10;
  width: 100%;
  border-radius: 40px;
  padding: 0;
  height: 40px;
}

.filter:after {
  content: "";
  position: absolute;
  top: 50%;
  right: 18px;
  transform: translateY(-50%);
  transform-origin: 50% 50%;
  width: 0px;
  height: 0px;
  border-style: solid;
  border-width: 5px 5px 0 5px;
  border-color: #0c0c0c transparent transparent transparent;
  z-index: 1;
}

.filter:hover {
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2);
}

.filter.is-active {
  z-index: 1000;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2);
}

.filter.is-active:after {
  top: 45%;
  right: 10px;
  transform: rotate(180deg);
}

.filter.is-active .filter__text {
  border-color: transparent;
  border-radius: 20px 20px 0 0;
}

.filter__text {
  position: relative;
  height: 40px;
  font-size: 16px;
  color: #0c0c0c;
  padding: 0 30px 0 20px;
  vertical-align: middle;
  line-height: 40px;
  user-select: none;
  border: 1px solid #c8c8c8;
  background-color: #fff;
  border-radius: 20px;
  z-index: 1;
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
}

.filter__text span {
  font-size: 16px;
  color: #0c0c0c;
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
}

.filter__list {
  position: absolute;
  display: none;
  left: 0px;
  right: 0px;
  top: 20px;
  padding: 20px 0 0;
  list-style: none;
  border-radius: 0 0 20px 20px;
  background: #fff;
  overflow: hidden;
  box-shadow: 0 0 30px rgba(0, 0, 0, 0.1);
}

.filter__list:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -5;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2);
}

.filter__link {
  position: relative;
  width: 100%;
  font-size: 14px;
  line-height: 36px;
  padding-left: 10px;
  color: #000;
  cursor: pointer;
  border-top: 1px solid #0c0c0c;
  transition: background-color .8s, border .8s, color .4s;
}

.filter__link:hover {
  background-color: #94251d;
  border-top: 1px solid #94251d;
  color: #fff;
}

.filter__row {
  position: relative;
  width: 100%;
  font-size: 14px;
  line-height: 36px;
  padding-left: 20px;
  color: #000;
  cursor: pointer;
  transition: 0.4s;
}

.filter__row:hover {
  background-color: rgba(204, 204, 204, 0.5);
}

.filter__row .btn {
  margin-top: 20px;
}

.filter__all {
  display: inline-block;
  color: #94251d;
  line-height: .8;
  border-bottom: 1px solid #94251d;
}

.filter__all:hover {
  border-color: transparent;
}

/* Rating */
.rating {
  display: inline-block;
  vertical-align: top;
  margin: 0 0 5px 20px;
  padding: 0;
  border: none;
}

.rating__caption {
  margin-bottom: 0.5em;
  padding: 0;
}

.rating__group {
  position: relative;
  width: 5em;
  height: 1em;
  background-image: url("../img/content/star-gray.svg");
  background-size: 1em 1em;
  background-repeat: repeat-x;
}

.rating__input {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
}

.rating__star {
  position: absolute;
  top: 0;
  left: 0;
  margin: 0;
  height: 1em;
  background-size: 1em 1em;
  background-repeat: repeat-x;
  cursor: pointer;
}

.rating__star:nth-of-type(1) {
  z-index: 5;
  width: 1em;
}

.rating__star:nth-of-type(2) {
  z-index: 4;
  width: 2em;
}

.rating__star:nth-of-type(3) {
  z-index: 3;
  width: 3em;
}

.rating__star:nth-of-type(4) {
  z-index: 2;
  width: 4em;
}

.rating__star:nth-of-type(5) {
  z-index: 1;
  width: 5em;
}

.rating__info {
  display: inline-block;
  vertical-align: top;
  margin-left: 10px;
  color: #bcbcbc;
}

.rating__input:hover,
.rating__input:checked + .rating__star {
  background-image: url("../img/content/star.svg");
}

.rating__input:hover ~ .rating__star {
  background-image: url("../img/content/star-gray.svg");
}

.check {
  position: relative;
  display: inline-block;
}

.check input {
  position: absolute;
  top: 2px;
  left: 0;
  z-index: 2;
  opacity: 0;
}

.check input:checked ~ span {
  user-select: none;
}

.check input:checked ~ span:before {
  background-color: #fff;
}

.check input:checked ~ span:after {
  content: "";
  position: absolute;
  top: 4px;
  left: 4px;
  display: inline-block;
  vertical-align: top;
  width: 10px;
  height: 8px;
  background-image: url("../img/content/icon-selector-checked.png");
}

.check input:disabled ~ span {
  opacity: 1;
}

.check span {
  position: relative;
  padding-left: 28px;
  left: 0;
  top: 0;
  font-size: 14px;
  color: #fff;
  cursor: pointer;
  user-select: none;
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
  z-index: 15;
}

@media only screen and (max-width: 380px) {
  .check span {
    display: inline-block;
    max-width: 88vw;
  }
}

.check span:before {
  content: "";
  position: absolute;
  top: -1px;
  left: 0;
  width: 18px;
  height: 18px;
  background-color: #fff;
  border-radius: 100%;
  border: 1px solid #ffbd00;
  cursor: pointer;
  transition: 0.4s;
}

